body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #cfcacf;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 20px;
  background: #262134;
  color: #e0e0e0;
}

.header-right {
  text-align: right;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.game-board {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-perspective: 600px;
          perspective: 600px;
  padding: 35px;
  width: 60%;
  margin: 20px auto;
  border-radius: 10px;
}

[id^="popup"] {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-inner {
  position: absolute;
  text-align: center;
  padding: 20px;
  left: 40%;
  right: 40%;
  top: 30%;
  bottom: 40%;
  margin: auto;
  background: white;
}

.button {
  border: none;
  color: #fcfaf9;
  margin: 12px;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  font-weight: 500;
}
.purple {
  background-color: #594881;
}
.grey {
  background-color: #dbdbdb;
  color: #594881;
}

.card {
  width: 128px;
  height: 178px;
  padding: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-sizing: border-box;
  text-align: center;
  margin-bottom: 15px;
  transition: 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: relative;
  white-space: nowrap;
}

.card div {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  transition: 0.6s;
  background: #f2eff1;
}

.card div.front {
  font-size: 50px;
  line-height: 120px;
  cursor: pointer;
  color: darken(#f2eff1, 20%);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.card div.back {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}

.back span {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.card.opened {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.card.matched {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.card.matched .back {
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.05) inset;
  opacity: 0.2;
}

/*
  IMAGES
*/
.tile-helper {
  display: inline-block;
  height: 100%;
}
.back img {
  position: absolute;
  top: 50%;
  height: 120px;
  margin-top: -60px; /* Half the height */
  margin-left: -60px;
}

